<template>
  <div class="main-inner post posts-expand">
    <div class="post-block">
      <article
        itemscope=""
        itemtype="http://schema.org/Article"
        class="post-content"
        lang="zh-CN"
      >
        <link
          itemprop="mainEntityOfPage"
          href="https://wbincs.gitee.io/2021/2/"
        />
        <header class="post-header">
          <h1 class="post-title" itemprop="name headline">关于</h1>
        </header>

        <div class="post-body" itemprop="articleBody">
          <div v-html="compileMarkdown"></div>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
import { marked } from "marked";
import hljs from "highlight.js";
import "highlight.js/styles/mono-blue.css";
marked.setOptions({
  renderer: new marked.Renderer(),
  highlight: function (code) {
    return hljs.highlightAuto(code).value;
  },
  pedantic: false, //默认为false。 尽可能地兼容 markdown.pl的晦涩部分。不纠正原始模型任何的不良行为和错误。
  gfm: true, //默认为true。 允许 Git Hub标准的markdown.
  tables: true, //默认为true。 允许支持表格语法。该选项要求 gfm 为true。
  breaks: false, //默认为false。 允许回车换行。该选项要求 gfm 为true。
  sanitize: false, //对输出进行过滤（清理）
  smartLists: true,
  smartypants: false, //使用更为时髦的标点，比如在引用语法中加入破折号。
  xhtml: false,
});

import { getAbout } from "@/api/about";
export default {
  name: "About",
  data() {
    return {
      content: "仅用来记录个人的学习、工作、生活",
      name: "About",
    };
  },
  mounted() {
    getAbout().then((response) => {
      this.content = response.data;
    });
  },
  computed: {
    compileMarkdown() {
      return marked(this.content);
    },
  },
};
</script>

<style lang="scss" scoped>
</style>
